// colors
$color: #000;
$background: #fff;

//font
$fonts: 'DM Sans', -apple-system, sans-serif;
$rem: 17px;
$mobileRem: 16px;

$headWeight: bold;

$break: 950px;

// margins
$margin: 0.5rem;

html {
    scroll-behavior: smooth;
}

html,
body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

body,
body * {
    box-sizing: border-box;
}

:root {
    font-size: $rem;

    @media (max-width: $break) {
        font-size: $mobileRem;
    }
}

body {
    background: $background;
    color: $color;
    // typography
    font-size: 1rem;
    font-family: $fonts;
    font-weight: normal;
    //
    letter-spacing: 1px;
    line-height: 1.5em;
}


// --------
//  STYLES
// --------

h1,
h2,
h3,
h4,
h5 {
    font-weight: $headWeight;
    text-align: center;
    text-transform: uppercase;
}

h1,h2 {
    font-size: 2rem;
    margin: 3rem 0;
    line-height: 2rem;
    @media (max-width: $break) {
        font-size: 1.5rem;
    }
}
h3 {
    font-size: 1rem;
    font-weight: bold;
    margin: 2rem 0;
}

p {
    letter-spacing: 0.03em;
}

a {
    display: block;
    text-decoration: none;
    color: $color;
    font-weight: bold;
    margin: 1rem 0;

    transition: all .2s;
    &:hover {
        opacity: 0.6;
    }
}

.button {
    display: inline-block;
    margin: 2rem 0;
    padding: 1rem 1.2rem;
    box-sizing: border-box;
    background: none;
    border: none;
    color: $color;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;

    background: $color;
    color: $background;

    transition: all .2s;
    &:hover {
        opacity: 0.8;
        transform: scale(1.02);
    }

    img {
        width: 1.1rem;
        height: 1.1rem;
        //display: inline-block;
        margin: 0;
        margin-right: 0.5rem;
        transform: translateY(3px);
    }
}

img {
    object-fit: cover;
}

p>img,
p>picture {
    width: 100%;
    margin: $margin*2 0 0 0;
}

hr {
    width: 8rem;
    margin: 3rem auto;
    border: solid 0.5px $color;
}


// --------
//  HEADER
// --------

#logo {
    position: fixed;
    top: 0;
    left: 2rem;

    width: 8rem;
    height: auto;

    z-index: 999;

    transition: all .4s;
}
.small {
    width: 5rem !important;
}

nav {
    position: fixed;
    top: 0;
    right: 0;
    padding: 2rem;
    display: flex;
    flex-flow: row nowrap;
    z-index: 999;

    transition: all .4s;

    a {
        margin: 0;
        margin-left: 2rem;
        text-transform: uppercase;
    }
}
.hidden {
    opacity: 0;
    visibility: hidden;
}

@media (max-width: $break) {
    nav {
        visibility: hidden;
    }
    #logo {
        width: 5rem !important;
        left: 1rem;
    }
    .small {
        opacity: 0;
        visibility: hidden;
    }
}

// --------
//   HERO
// --------

.hero {
    width: 100%;
    height: 80vh;

    margin-top: 5.5rem;

    display: flex;
    align-items: center;
    justify-content: center;

    $videoSize: 600px;

    video {
        width: 100%;
        height: auto;
        max-width: $videoSize;
        max-height: $videoSize;
    }

    img {
        width: 100%;
        height: 100%;
        overflow: hidden;
        object-fit: cover;
        object-position: top;
    }

    @media (max-width: $break) {
        margin-top: 4rem;
    }
}

// -----------
//  CONTAINER
// -----------

.container {
    margin: 0 auto;
    width: 100%;
    max-width: $break;
    padding: $margin;

    text-align: center;
}

section {
    display: block;
    width: 100%;
    max-width: $break;
    margin: 8rem auto;
    padding: 0 1rem;
    text-align: center;

    img {
        max-width: 100%;
    }
}
.grijs {
    background: #eee;
    width: 100%;
    max-width: 100%;
    margin-left: 0;
    margin-right: 0;
    padding: 1rem calc(50% - #{$break * 0.5});
    @media (max-width: $break) {
        padding: 0.5rem;
    }
}

footer {
    display: block;

    color: $background;
    background: $color;

    margin: 4rem 0 0 0;
    padding: 2rem 0;
    width: 100%;

    a {
        display: block;
        margin: 1rem 0;
    }

    * {
        color: $background;
        text-align: left;
        font-size: 0.8rem;
        margin: 0 !important;
    }

    .container {
        margin: 0 auto !important;
        padding: 0 1rem;
    }
}
.row-container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    width: 100%;

    img {
        width: 200px;
        height: auto;
        object-fit: contain;
    }

    @media (max-width: 600px) {
        display: block;

        img {
            display: none;
        }
    }
}


// -------
//  INSTA
// -------

.instagram_gallery {
    box-sizing: border-box;
    display: flex;
    flex-flow: row wrap;
    position: relative;
    height: auto !important;
    margin: $margin 0;
    width: 100%;

    a {
        margin: $margin*0.1;
        overflow: hidden;
        display: block;
        width: calc(33% - #{$margin * 0.1} - 1px);
        height: auto;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        transition: all .2s;
        * {
            transition: all .2s;
        }

        &:hover {
            
            img {
                transform: scale(1.05);
                opacity: 0.8;
            }
        }
    }
}